import React from 'react';
import { Link } from 'react-router-dom';

function Navigation(){
    return(
        <nav>
            <Link className='App-Link' to='/'>Home</Link>
            
            <Link className='App-Link' to='../shopping'>Shop</Link>
            
            <Link className='App-Link' to='../store'>Store List</Link>
        </nav>
    );
}

export default Navigation;