import React, {useState} from 'react';
//onClick catches zip codes that are too big or too small with a conditinal chain
function ZipSearch(){
    const [zipCode, setZip]=useState('');
    return(
        <form action='' method=''>
            <fieldset>
                <legend>Enter your zip code:</legend>
                <label htmlFor='zip' >Zip Code:</label>
                    <input type='number' required value={zipCode}  id='zip' name='zipCode' placeholder='00000' min='11111' max='99999' 
                    onChange={e => setZip(e.target.value)} />            
            <label htmlFor ='popup'>
                <button name='popup' id='popup' 
                onClick={`${zipCode}`> 99999 ? e =>{alert(`Invalid zip code. Try again` ); e.preventDefault();}
                : `${zipCode}`.length < 5 ? e =>{alert(`Invalid zip code. Try again` ); e.preventDefault();}
                : `${zipCode}`.length > 5 ? e =>{alert(`Invalid zip code. Try again` ); e.preventDefault();}
                : `${zipCode}`< 501 ? e =>{alert(`Invalid zip code. Try again` ); e.preventDefault();}
                : e =>{alert(`You entered ${zipCode}. Nice work`);e.preventDefault();}}
                >Submit</button>
            </label>
            </fieldset>
        </form>
    
    );
};

export default ZipSearch;