import React from 'react';
import { DiCloud9 } from "react-icons/di";

function HomePage() {
    return (
    <>
     <article className='App-article'>
         <h2>
             Welcome to the home page!
         </h2>
         <p>
             Please navigate to Shop to add to cart or Store List to see our locations.
         </p>
         <DiCloud9 className='App-logo'/>
     </article>
     </>
    );
  }
  
  export default HomePage;