import React, {useState} from 'react';
import {FcDown,FcUp} from 'react-icons/fc';
//import {FaSmileBeam,FaSkullCrossbones} from "react-icons/fa";

function SelectQuantity(){
    const [itemNumber, setTotal]=useState(0);
    const toogleAdd=()=>setTotal(itemNumber===10 ? itemNumber+0: itemNumber+1)
    const toogleDec=()=>setTotal(itemNumber<=0 ? itemNumber+0: itemNumber-1)
    
    return(  
        <>
        <FcDown onClick={toogleDec}/>
        <span>{itemNumber}</span>
        <FcUp onClick={toogleAdd}/>
      </>
    );
};

export default SelectQuantity;